@mixin header-alumni {
  margin-top: 0;
  margin-bottom: 0;

  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;

  color: $color-default-white;
}

@mixin header-2 {
  @include header-alumni();
  font-size: 54px;
  line-height: 65px;
  margin-bottom: 25px;

  color: $color-default-black;
}

@mixin header-2-text {
  @include header-alumni();
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 25px;

  color: $color-default-black;
}

@mixin header-2-white {
  @include header-alumni();
  font-size: 48px;
  text-transform: uppercase;
}

@mixin event-header {
  @include header-alumni();
  font-size: 82px;
  line-height: 78px;

  margin-bottom: 35px;
}

@mixin form-header {
  @include header-alumni();
  font-size: 54px;
  font-weight: 800;
  line-height: 82px;

  margin-bottom: 25px;

  color: $color-default-white;
}

@mixin contact-header {
  @include header-alumni();
  font-size: 54px;
  line-height: 65px;

  color: $color-default-black;
}

@mixin header-roboto {
  margin-top: 0;
  margin-bottom: 22px;

  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;

  color: $color-default-black;
}

@mixin card-header {
  @include header-roboto();
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 0;

  color: $color-default-white;
}

@mixin advantage-header {
  @include header-roboto();
  font-size: 22px;
  line-height: 33px;

  margin-bottom: 20px;
  color: $color-default-black;
}

@mixin offer-price-header {
  @include header-roboto();
  font-size: 70px;
  line-height: 109px;
  margin-bottom: 35px;

  color: $color-default-white;
}

@mixin offer-header {
  @include header-roboto();
  font-size: 26px;
  line-height: 32px;
  margin-bottom: 20px;

  color: $color-default-black;
}

@mixin review-header {
  @include header-roboto();
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 25px;

  color: $color-default-white;
}

@mixin text {
  font-family: $ff-roboto;
}

@mixin white-text {
  font-family: $ff-roboto;
  font-size: 18px;
  line-height: 25px;
  font-weight: 400;
  color: $color-default-white;
}

@mixin white-link {
  @include white-text();

  @include hover() {
    text-decoration: underline;
  }

  &:focus {
    text-decoration: underline;
  }
}

@mixin white-promo-link {
  @include white-text();
  text-transform: uppercase;
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 1px)) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: ($vp-1280 - 1px)) {
    @content;
  }
}

@mixin vp-1200 {
  @media (max-width: ($vp-1200 - 1px)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: ($vp-375 - 1px)) {
    @content;
  }
}

